import React, {useState} from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {cilLockLocked, cilUser} from '@coreui/icons'

import axios from 'axios'
import {useForm} from "react-hook-form";
import {ErrorMessage} from '@hookform/error-message';
import AdminLoginBackground from "../../../assets/images/travel.jpg";

import TDO_Logo from "../../../assets/images/full-logo.png";
import {tokenName, ipAddress, successToast, errorToast} from "../../../utils/Common";
import {useNavigate} from "react-router-dom";
import {ToastContainer, toast} from 'react-toastify';

const Register = () => {
  let {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({
    defaultValues: {
      // username: 'support@traveldealsonline.com',
      // password: 'Tdo@1ZUIK1O',
    }
  });

  const [btnDisabled, setBtnDisabled] = useState(false);
  let navigate = useNavigate();

  function onSubmit(data) {
    setBtnDisabled(true);

    // const endPoint = "login"; // old
    const endPoint = "admin/login"; // new

    axios.post(ipAddress + endPoint, data)
      .then((response) => {
        // console.log(response.data);

        setBtnDisabled(false);
        if (!response.data.responseCode) {
          // alert("Invalid Response")
          errorToast("Invalid Response");
        } else {
          if (response.data.responseCode === 1) {
            errorToast("Login failed");
          } else {
            if (!response.data.token) {
              // alert("No Token Found");
              errorToast("No Token Found");
            } else {
              successToast("Login Successful");

              const {token} = response.data;
              setCookie(tokenName, token, 24);
              setTimeout(() => navigate("/dashboard"), 1000);
            }
          }
        }
      }).catch(e => {
      // console.log(e);
      setBtnDisabled(false);
      errorToast(e.message);
    })
  }

  function setCookie(cookieName, cookieValue, expirationHours) {
    const d = new Date();
    d.setTime(d.getTime() + expirationHours * 60 * 60 * 1000);

    const expires = "expires=" + d.toUTCString();
    document.cookie = `${cookieName}=${cookieValue}; ${expires}; path=/; SameSite=Strict`;
  }

  return (
    <div style={{
      backgroundImage: `url(${AdminLoginBackground})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: '100% 100%'
    }} className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={9} lg={7} xl={6}>
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <h3 style={{textAlign: "center", color: "red"}}><b>New Mid Office</b></h3>
                <div className="d-flex justify-content-center">
                  <img src={TDO_Logo} className="rounded-3"
                       style={{backgroundColor: 'red', backgroundSize: '100% 100%'}} alt=""/>
                </div>
                <br/>

                <CForm onSubmit={handleSubmit(onSubmit)}>
                  <div className="d-flex justify-content-center">
                    <p className="text-medium-emphasis">Login to your account</p>
                  </div>

                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilUser}/>
                    </CInputGroupText>
                    <CFormInput {...register('username', {required: 'This is a required field.'})}
                                type="text"
                                placeholder="Username"
                                autoComplete="username"
                    />
                  </CInputGroup>
                  {errors.username && <p className='text-danger'>This is a required field.</p>}

                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked}/>
                    </CInputGroupText>
                    <CFormInput
                      {...register('password', {required: true})}
                      type="password"
                      placeholder="Password"
                      autoComplete="new-password"
                    />
                  </CInputGroup>
                  {errors.password && <p className='text-danger'>This is a required field.</p>}

                  <div className="d-grid">
                    <CButton style={{backgroundColor: 'red', color: 'white', border: 'black'}}
                             type='submit' color="success" disabled={btnDisabled}>Login</CButton>
                  </div>
                </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>

      <ToastContainer/>
    </div>
  )
}

export default Register
